@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

@keyframes fill-animation {
  0% {
    fill-opacity: 0; }
  50% {
    fill-opacity: 0; }
  100% {
    fill-opacity: 1; } }

@keyframes deslocar {
  0% {
    transform: translateX(40%); }
  100% {
    transform: translateX(0); } }

@keyframes the-animation {
  0% {
    fill-opacity: 0;
    transform: translateY(40%); }
  50% {
    transform: translateY(-20%); }
  100% {
    fill-opacity: 1;
    transform: translateY(0%); } }

.Logo {
  width: 220px; }
  .Logo .st0 {
    animation: fill-animation 2.5s forwards;
    fill: #02d8c1;
    stroke: #02d8c1;
    stroke-width: 0.75;
    stroke-miterlimit: 10; }
  .Logo .st1 {
    animation: fill-animation 2.5s forwards;
    fill: #5d66e9;
    stroke: #5d66e9;
    stroke-width: 0.75;
    stroke-miterlimit: 10; }
  .Logo .st2 {
    animation: fill-animation 2.5s forwards;
    fill: #ffc400;
    stroke: #ffc400;
    stroke-width: 0.75;
    stroke-miterlimit: 10; }
  .Logo .st3 {
    fill-opacity: 0;
    animation: fill-animation 2.5s forwards;
    fill: #e61c1c;
    stroke: #e61c1c;
    stroke-width: 0.75;
    stroke-miterlimit: 10; }
  @media only screen and (min-width: 1024px) {
    .Logo .st-g {
      transform: translateX(40%);
      transform-origin: 50%;
      animation: deslocar 0.5s ease-out forwards;
      animation-delay: 3.25s; } }
  .Logo .st5 {
    fill-opacity: 0;
    display: none;
    fill: #1d2126; }
    @media only screen and (min-width: 1024px) {
      .Logo .st5 {
        display: inherit;
        animation: the-animation 0.5s ease-out forwards; } }
  .Logo .st-m {
    animation-delay: 3.7s; }
  .Logo .st-e {
    animation-delay: 3.8s; }
  .Logo .st-u {
    animation-delay: 3.9s; }
  .Logo .st-h {
    animation-delay: 4s; }
  .Logo .st-o {
    animation-delay: 4.1s; }
  .Logo .st-r {
    animation-delay: 4.2s; }
  .Logo .st-a {
    animation-delay: 4.3s; }
  .Logo .st-rr {
    animation-delay: 4.4s; }
  .Logo .st-i {
    animation-delay: 4.5s; }
  .Logo .st-oo {
    animation-delay: 4.6s; }
