@import url("https://rsms.me/inter/inter-ui.css");
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important; } }

html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden; }

input,
textarea,
button,
select,
a,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; }

::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px; }

@supports (font-variation-settings: normal) {
  body {
    font-family: 'Inter UI var alt', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif; } }

.MainPage {
  flex: 1 1;
  display: flex;
  overflow: hidden;
  padding: 0 0.5rem; }

.mainTable {
  flex: 1 1;
  height: 100%;
  overflow-y: scroll; }

.sideBar {
  position: fixed;
  top: calc(100vh - 4.5rem);
  left: 0;
  width: 100%;
  background: #ffffff;
  height: 100%;
  overflow: hidden;
  border: 2px solid #e7eef3;
  border-radius: 0.5rem;
  box-shadow: 0 -5px 50px rgba(163, 194, 217, 0.5);
  z-index: 99;
  transition: top 0.3s ease-out; }
  .sideBar > div {
    margin: 0 0.75rem; }
  .sideBar .buttonDiv {
    margin-top: 0.75rem; }
  @media only screen and (min-width: 1024px) {
    .sideBar {
      position: static;
      padding: 0 1rem;
      flex: 0 1 16rem;
      border: none;
      border-radius: 0;
      box-shadow: none;
      overflow-y: scroll; }
      .sideBar > div {
        margin: 0; }
      .sideBar .buttonDiv {
        display: none; } }
  @media only screen and (min-width: 1280px) {
    .sideBar {
      position: static;
      padding: 0 1.5rem;
      flex: 0 1 20rem; } }
  .sideBar.show {
    top: 0rem;
    overflow-y: scroll; }

::-webkit-scrollbar {
  display: block;
  width: 0.5rem;
  position: absolute; }

::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 0.5rem; }

:hover::-webkit-scrollbar-track {
  background: rgba(231, 238, 243, 0.5); }

:hover::-webkit-scrollbar-thumb {
  background: #5b778c; }
